<script>
  import Brief from './brief.vue'
  import Technology from './technology.vue'
  import Job from './jobDream.vue'
  import Work from './working.vue'
  import Suffer from './suffer.vue'
  export default {
    name: 'resume',
    data () {
      return {
        hotIndex: 0
      }
    },
    methods: {
      linkTo (index, url) {
        this.hotIndex = + index
        this.$router.push({path: url})
      }
    },
    components: {
      Brief,
      Technology,
      Job,
      Work,
      Suffer
    },
    mounted () {
      var path = this.$route.path
      switch (path) {
        case '/':
          this.hotIndex = 0
          break;
        case '/brief':
          this.hotIndex = 0
          break;
        case '/technology': 
          this.hotIndex = 1
          break;
        case '/job':
          this.hotIndex = 2
          break;   
        case '/working':
          this.hotIndex = 3
          break;
        case '/suffer':
          this.hotIndex = 4
          break;  
        case '/project':
          this.hotIndex = 5
          break;  
        case '/aboutme':
          this.hotIndex = 6
          break;
        default:
          this.$router.push({path: '/'})
      }
    }
  }

</script>

