<template>
    <div class="suffer">
        <h5>创业经历</h5>
        <div class="content">
            <div style="text-align: right;">
                <el-Button @click="toggleMusic">{{musicIsOpen ? '关闭' : '播放'}}音乐</el-Button>
            </div>
            <p style="font-size:16px;">- - 理想总是很丰满，现实却很骨感。</p>
            <p>这可能是一个悲伤、很真实的故事。</p>
            <p>2017年,那年夏天,创业之风迎面而来。在深圳这个创业的摇篮里,许许多多的ideas、各种政策倾斜、层出不穷的市场需求、永远不差钱的风投家们,于是诞生了无数渴望成功的宝宝们。
                我便是其中一个被吸引的孩子,目标很明确，定位很精准，行动很迅速，我就是要迎合市场需求、快速地从风投市场、从盲目从众的中国消费群体中开拓出我们的道路，
                赚得一桶金以后我就不用再被生计所困扰，甚至可以过自己想要的生活。
            </p>
            <p>
                于是乎，我毅然辞去"朝九晚六、三点一线"稳定的公司生活，靠着圈内的人脉关系，成功混进去了一个创业团队里面。年轻气盛的我们，很有冲劲，团队leader对于规划的饼画得很大，
                大到你甚至觉得只要团队每个人付出了努力，以后的回报已经溢出你的认知;或者正是因为饼大，大到遮住了本应该平行于市场的视线，总是感觉自己一定可以成功捞金。
            </p>
            <p>
                你可能很难想象，一个小团队的人甚至可以在连续三个月内换了五批人员是什么情况;
                你可能很难想象，创业团队里面的压力、执行模式其实不亚于传销活动;
                你可能很难想象，连续三个月从早上九点上班到深夜十二点是什么体验，没有双休，只有无尽的加班，无尽的需求。
                你可能很难想象, 到了后期，因为缺乏资金，我们甚至基本工资都发不来还在咬牙坚持...
                因为我们要快速出来产品，因为我们要控制在低成本，
                就注定了我们团队的规模在前期根本不可能做大;
                但是在如此高压、高频繁、缺乏人力的催命式工作下，
                出来的产品如何以高质量、如何以优质的用户体验来面向市场;
                一个好的产品，不可能是一蹴而就的;
                没有产品的迭代，做不到产品的优化与升级;
                没有广大的用户响应，你根本不知道你闭门造车出来的东西是否能满足大众需求;
                没有长期的运营推广,如何让你的产品众所周知;
                而没有长期的技术积累，做出来的产品又如何能凌驾于市场之上?
            </p>
            <p>
                诸如这些，其实都是很浅显的道理，可正是当局者迷。直到后来某天经过药店凑巧去称体重，发现自己整整瘦了20斤，打开手机，照下镜子，啊，这还是我吗？
                整个人瘦了一圈，憔悴的样子像极了"东亚病夫",肿黑的眼圈堪比大熊猫，整个人看起来像被蒸干了一样。此刻，所有浅显的道理幡然醒悟。
                所有的饼，哪怕画得再大，也得有命去吃；这并不是正常的、合理的创业方式，这是病态的、畸形的、违背常理与市场的，注定是失败的。
                翌日，脱离了那个整整折磨了我三个月的深潭。
            </p>
            <p>那个企图以某会员管理系统“统治”所有酒店、餐饮等服务业的发财泡沫梦就此破灭，那个睡梦中的人再一次回到现实，脚踏实地的走好下一步。</p>
        </div>
        <audio src="background.mp3" autoplay id="music">
            您的浏览器不支持播放音乐。
        </audio>
    </div>
</template>

<script>
    export default {
        name: 'suffer',
        data() {
            return {
                musicIsOpen: true
            }
        },
        methods: {
            toggleMusic() {
                var music = document.getElementById('music');
                if (this.musicIsOpen) {
                    music.pause()
                } else {
                    music.play()
                }
                this.musicIsOpen = !this.musicIsOpen
            }
        },
        mounted() {
            this.$nextTick(() => {
                var music = document.getElementById('music');
                music.currentTime = 0
            })
        }
    }

</script>

<style lang="less" scoped>
    .suffer {
        h5 {
            margin: 0;
            border-bottom: 1px solid #eee;
            line-height: 30px;
        }
        .content {
            margin-top: 40px;
            text-align: left;
            font-size: 14px;
            p {
                text-indent: 30px;
                line-height: 30px;
            }
        }
    }
</style>